export default class FilterPicker extends Component {
  state = {
    // 
    value: this.props.value,
  }
  render() {
    const { data, cols } = this.props
    return (
      <>
        {/*  */}
        <PickerView
          data={data}
          value={this.state.value}
          onChange={(v) => {
            this.setState({
              value: v,
            })
          }}
          cols={cols}
        />

        {/*  */}
        <FilterFooter
          onCancle={this.props.onCancle}
          onOk={() => {
            this.props.onOk(this.state.value)
          }}
        />
      </>
    )
  }
}
